.Container{
  position: absolute;
  background-color: rgb(0,0, 0, 0.5 );
  min-width: 100%;
  min-height: 100%;
  z-index: -15;
  color: white;
  
}

button{
  position: absolute;
  background-color: white;
  color: black;
  text-align: center;
  text-decoration: none;
  font-size: 25px;
  cursor: pointer;
  border-radius: 5px;
  top: 475px;
  left: 30px;
  width: 500px;
  height: 45px;
}

button:hover{
  background-color: green;
  color: white;
}

.YourName, .PartnerName{
  position: relative;
  top: 200px;
  left: 30px;
  
  }
  
input{
    width:500px;
    padding: 8px 10px;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-size: 20px;
   }

